import React from 'react';
import PropTypes from 'prop-types';
import Link from 'gatsby-link';
import classNames from 'classnames/bind';
import Tappable from 'react-tappable/lib/Tappable';

class Header extends React.Component {
  state = {
    isActive: false,
  };

  static propTypes = {
    siteTitle: PropTypes.string.isRequired,
  };

  handleMenuAction = () => {
    const { isActive } = this.state;
    this.setState({ isActive: !isActive });
  };

  render() {
    const { siteTitle } = this.props;
    const { isActive } = this.state;
    return (
      <div
        style={{
          marginBottom: '1.45rem',
        }}
      >
        <nav className="navbar is-fixed-top is-dark">
          <div className="container">
            <div className="navbar-brand">
              <Link to="/" className="navbar-item">
                {siteTitle}
              </Link>
              <Tappable
                component="div"
                className={classNames({
                  'navbar-burger': true,
                  burger: true,
                  'is-active': isActive,
                })}
                aria-pressed="false"
                data-target="navbarFixedTop"
                onTap={this.handleMenuAction}
              >
                <span aria-hidden="true" />
                <span aria-hidden="true" />
                <span aria-hidden="true" />
              </Tappable>
            </div>

            <div
              id="navbarFixedTop"
              className={classNames({
                'navbar-menu': true,
                'is-active': isActive,
              })}
            >
              <div className="navbar-start">
                <Link className="navbar-item" to="/projects/">
                  Projects
                </Link>
                <Link className="navbar-item" to="/tech/">
                  Technology
                </Link>
              </div>

              <div className="navbar-end">
                <div className="navbar-item">
                  <div className="field is-grouped">
                    <p className="control">
                      <a
                        className="bd-tw-button button"
                        target="_blank"
                        rel="noopener noreferrence"
                        href="https://twitter.com/shadez95"
                      >
                        <span className="icon">
                          <i className="fab fa-twitter" />
                        </span>
                        <span>Twitter</span>
                      </a>
                    </p>
                    <p className="control">
                      <a
                        className="bd-tw-button button"
                        target="_blank"
                        rel="noopener noreferrence"
                        href="https://github.com/shadez95"
                      >
                        <span className="icon">
                          <i className="fab fa-github" />
                        </span>
                        <span>Github</span>
                      </a>
                    </p>
                    <p className="control">
                      <a
                        className="bd-tw-button button"
                        target="_blank"
                        rel="noopener noreferrence"
                        href="mailto:contact@dixonbegay.com?body=%0D%0D%0D%0D%0DSent%20from%20dixonbegay.com"
                      >
                        <span className="icon">
                          <i className="fas fa-envelope" />
                        </span>
                        <span>Email</span>
                      </a>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </nav>
      </div>
    );
  }
}

export default Header;



